




































	import header from "../../../components/header.vue"
	export default { 
		components:{
			headers:header
		},
		data(){
			return {
				imgSrc:"../../../static/img/back.png",
				title:"设置密码",
				user:"",
				pass:"",
				phone:"",
				ms:""
				// imgSrc:imgs
			}
		},
		onLoad(val){
			this.phone = val.id;
			this.ms = val.name;
		},
		methods:{
			back(){
				uni.navigateBack();
			},
			goInfo(){
				uni.navigateTo({
				    url: '/pages/page/info/info'
				});
			},
			
			isLogin(){
				 if (this.user.length < 6) {
						 uni.showToast({
							icon:"none",
							title: '密码必须大于6个字符',
							duration: 2000
						});
						return false;
                     } 
                     if (this.pass.length < 6) {
                         uni.showToast({
							 icon:"none",
                         	title: '确认密码长度大于6',
                         	duration: 2000
                         });
                         return false;
                     } 
					 if(this.user != this.pass){
						 uni.showToast({
							icon:"none",
							title: '两次密码不一致',
							duration: 2000
						});
						return false;
					 }
					 this.login();
			},
			login(){
				uni.request({
					url: this.userUrl + "/BtCApi/Login/UpdatePassWord", //仅为示例，并非真实接口地址。
					method:'POST',
					 header: {
						'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					data: {
						LoginPass: this.user,
						OkPass: this.pass,
						Mob: this.phone,
						Verification_Code: this.name,
						DeviceId: '',
						PhoneVersion: '',
						ClientVersion: '',
						ClientType: '',
					},
					success: (res) => {
						console.log(res.data);
						if(res.data.status == true && res.data.code == 30000){
							uni.navigateTo({
								url:"/pages/page/login/login"
							})
						}else{
							uni.showToast({
								icon:"none",
								title: res.data.msg,
								duration: 2000
							});
						}
					}
				});
			}
		}
	}
